.upf-hide-default-title .entry-title {
  display: none;
}

.upf-content {
  position: relative;
}

.upf-content *:not([class*="upf-content"]) {
  display: none !important;
}

.upf-content--light {
  color: #ffffff;
}

.upf-content--dark {
  color: #000000;
}

.upf-content iframe {
  display: block;
  width: 100%;
  margin: 0;
}

.upf-content__permalink {
  height: 100%;
  position: absolute;
  width: 100%;
  cursor: pointer;
  left: 0;
  top: 0;
}

.upf-content__title {
  color: inherit !important;
  margin: 0 !important;
  padding: 0 !important;
}
